import fetch from 'utils/fetch';

export function loginByEmail(email, password) {
    const data = {
        email,
        password
    };
    return fetch({
        url: '/login/loginbyemail',
        method: 'post',
        data
    });
}

export function logout() {
    return fetch({
        url: '/login/logout',
        method: 'post'
    });
}

export function getInfo(token) {
    return fetch({
        url: '/user/info',
        method: 'get',
        params: {token}
    });
}

export function getPage(page, size) {
    return fetch({
        url: '/user/list',
        method: 'get',
        params: {page, size}
    });
}



